// =========================//
// custom setup detection for mobile or tablet
// =========================//

function handleMatchMedia() {
  window.isMobileOrTablet = window.matchMedia("only screen and (max-width: 1025px)").matches;
  window.isMobile = window.matchMedia("only screen and (max-width: 767px)").matches;
  var prevLayout = window.isMobileOrTablet ? 'mobile' : 'desktop';
  var width = $(window).width();
  var height = $(window).height();

  $(window).on('resize', _.debounce(function() {
    window.isMobileOrTablet = window.matchMedia("only screen and (max-width: 1025px)").matches;
    window.isMobile = window.matchMedia("only screen and (max-width: 767px)").matches;

    if (prevLayout === 'mobile' && !window.isMobileOrTablet ) {
      document.dispatchEvent( new Event('resized-to-desktop') );

    } else if (prevLayout === 'desktop' && window.isMobileOrTablet) {
      document.dispatchEvent( new Event('resized-to-mobile') );
    }
    prevLayout = window.isMobileOrTablet ? 'mobile' : 'desktop';

    // AUTO CLOSE FOR MODALS (minibag)
    // vertical resize, abort || orientation change, abort
    if ($(window).width() == width || $(window).width() == height) {
      return;
    }
    // still here? close the modal
    if (!window.isMobileOrTablet) {
      $('.modal--minibag').modal('hide');
    }
    // update the vals for next time
    width = $(window).width();
    height = $(window).height();

  }, 250));

}
handleMatchMedia();

