// Nav
// ===========================
var offCanvas = require("./modules/navigation/off-canvas");


function megamenuInit() {

  require("./modules/navigation/menu-accessible");
  var parentNavMenu = $('[data-app="accessible-navmenu"]');
  parentNavMenu.accessibleMegaMenu({
    /* prefix for generated unique id attributes, which are required to indicate aria-owns, aria-controls and aria-labelledby */
    uuidPrefix: "accessible-menu",
    menuClass: "c-navbar",
    topNavItemClass: "navbar__top-item",
    panelClass: "sub-nav",
    panelGroupClass: "sub-nav-group",
    hoverClass: "hover",
    focusClass: "focus",
    openClass: "open"
  });
}



// on load
if (!window.isMobileOrTablet) {
  megamenuInit();
}
if (window.isMobileOrTablet) {
  offCanvas.start();
}

// after resize
document.addEventListener('resized-to-mobile', function() {
  offCanvas.start();
});

document.addEventListener('resized-to-desktop', function() {
  offCanvas.stop();
  megamenuInit();
});







