require('./vendors/bootstrap/tooltip');
require('./vendors/bootstrap/popover');

$(document).ready(function() {

  $(function() {

    // regular, all except filter colours
    $('[data-toggle="tooltip"]:not(.filter__colour)').tooltip({
      placement: 'top',
      viewport: { 'selector': 'body', 'padding': 0 }
    });

    // filter colours (not on on mobile)
    if (!window.isMobileOrTablet) {
      $('[data-toggle="tooltip"].filter__colour').tooltip({
        placement: 'top',
        viewport: { 'selector': 'body', 'padding': 0 }
      });
    }

    $('.o-form--default [data-toggle="tooltip"]').tooltip({
      placement: 'left',
      viewport: { 'selector': 'body', 'padding': 0 }
    });

    $('.o-form--wide [data-toggle="tooltip"]').tooltip({
      placement: 'right',
      viewport: { 'selector': 'body', 'padding': 0 }
    });
  });

  $(function() {
    $('.o-form--default [data-toggle="popover"]').popover({
      placement: 'left',
      viewport: { 'selector': 'body', 'padding': 0 }
    });
    $('.o-form--wide [data-toggle="popover"]').popover({
      placement: 'right',
      viewport: { 'selector': 'body', 'padding': 0 }
    });
  });

  $('form[data-checkdirty]').dirrty();
});

$('body').on('shown.bs.modal', function() {
  $('form[data-checkdirty]').dirrty();
});


