var heightFix = function() {
  var h = Math.max($('.o-fullscreen-container__box--large').outerHeight(), $('.o-fullscreen-container__box').outerHeight());
  var headerHeight = $('.o-header__container').height();
  var viewPortHeight = $( window ).height() - headerHeight;

  var actualHeight = viewPortHeight - headerHeight;
  var finalHeight = Math.max(h, actualHeight);
  $('.js-height-fix').height(finalHeight + 60);
}

heightFix();
$(window).on('resize', _.throttle(function() {
    heightFix();
}, 500));



