// async script loader
// could move this to a utils module as also used in google maps
const loadScript = (src, callback) => {
    const script = document.createElement("script");
    script.type = "text/javascript";
    if (callback) {
        script.onload = callback;
    }
    document.getElementsByTagName("head")[0].appendChild(script);
    script.src = src;
};


// step 1, we'll start async loading of bazaar voice
loadScript('https://matalan.ugc.bazaarvoice.com/bvstaging/static/4567-en_gb/bvapi.js', initializeSubmit);

function initializeSubmit() {
    var userToken = $('#BVSubmissionContainer').data('usertoken').toUpperCase();
         
    $BV.ui("submission_container", {
        userToken: userToken
    });
    

}



