let charLength = 0;

const cheapIsElHidden = el => el.offsetParent === null;

const showPopover = function() {
  $(this).popover('show');
};

const hidePopover = function() {
  charLength = 0;
  $(this).popover('hide');
};


$('[data-validation="password"]').popover({
    trigger: 'manual',
    html: true,
    placement(context, source) {
      const offset = ($(window).width() - ($(source).offset().left + $(source).outerWidth()));
      return 'top';
    },
    content(context, source) {
      const thisId = $(source).attr('id');
      return "<p><strong>Passwords must have:</strong></p><ul class='o-list__validation'><li class='pass-check-1'>8 to 32 characters</li></ul><p><strong>and include 2 of the below</strong></p><ul class='o-list__validation'><li class='pass-check-2'>Upper and lowercase letters</li><li class='pass-check-3'>1 number</li><li class='pass-check-4'>1 special character</li></ul>"
    }
  })
  .focus(showPopover)
  .blur(hidePopover)
  .keyup(function() {

    if (this.value.length >= 8 && this.value.length <= 32) {
      $('.pass-check-1').addClass('success');
    } else {
      $('.pass-check-1').removeClass('success');
    }

    /*contains lowercase and uppercase characters*/
    if (this.value.match(/[a-z]+/) && this.value.match(/[A-Z]+/)) {
      $('.pass-check-2').addClass('success');
    } else {
      $('.pass-check-2').removeClass('success');
    }

    /*contains digits*/
    if (this.value.match(/[0-9]+/)) {
      $('.pass-check-3').addClass('success');
    } else {
      $('.pass-check-3').removeClass('success');
    }

    if (this.value.match(/[$£&+,:;=?@#|'<>.^*()%!\-_]+/)) {
      $('.pass-check-4').addClass('success');
    } else {
      $('.pass-check-4').removeClass('success');
    }

  });

$('[data-validation="password-confirm"]').popover({
    trigger: 'manual',
    html: true,
    placement(context, source) {
      const offset = ($(window).width() - ($(source).offset().left + $(source).outerWidth()));
      // console.log(offset);
      // if (offset < 300) return 'top';
      return 'top';
    },
    content(context, source) {
      return "<p><strong>Passwords must correctly match:</strong></p><ul class='o-list__validation'><li class='pass-confirm-check-1'>Passwords match</li></ul>";
    }
  })
  .focus(showPopover)
  .blur(hidePopover)
  .keyup(function() {
    const password = $('[data-validation="password"]').val();
    const confirmPassword = $('[data-validation="password-confirm"]').val();
    if (password === confirmPassword && this.value.length >= 8) {
      $('.pass-confirm-check-1').addClass('success');
    } else {
      $('.pass-confirm-check-1').removeClass('success');
    }
  });



//Character restriction
const validateTypeaheadLength = (button, e) => {
  const closestInput = $(button).closest('.o-form__row').find('input.tt-input');
  const inputLength = (closestInput).val().replace(/ /g, '').length;
  const minLength = parseInt($(closestInput).attr('data-minlength'));
  if (inputLength < minLength) {

    $(closestInput).popover('show').on('shown.bs.popover', function() {
      const $pop = $(this);
      setTimeout(() => {
        $pop.popover('hide');
      }, 2000);
    });
    e.preventDefault();
  }
};


const validateEmail = (button, e) => {
  const closestInput = $(button).closest('.o-form__row').find('input');
  const email = $(closestInput).val();
  const re = /^(([^<>()\[\]\\.,;:\s@"]+(\.[^<>()\[\]\\.,;:\s@"]+)*)|(".+"))@((\[[0-9]{1,3}\.[0-9]{1,3}\.[0-9]{1,3}\.[0-9]{1,3}])|(([a-zA-Z\-0-9]+\.)+[a-zA-Z]{2,}))$/;
  if (re.test(email) === false) {
    console.log('failed')
    $(closestInput).popover('show').on('shown.bs.popover', function() {
      const $pop = $(this);
      setTimeout(() => {
        $pop.popover('hide');
      }, 2000);
    });
    e.preventDefault();
  }
};

const validateTelephone = telephone => {
  telephone = telephone.replace(/\s/g, "");
  const regex = /^0(\d ?){10}$/i;
  return regex.test(telephone);
};


const validPostcode = postcode => {
  postcode = postcode.replace(/\s/g, "");
  const regex = /^[A-Z]{1,2}[0-9]{1,2} ?[0-9][A-Z]{2}$/i;
  return regex.test(postcode);
};

const integerCheck = value => {
  value = value.replace(/\s/g, "");
  const regex = /\d{11}/;
  return regex.test(value);

}


const removeFormErrors = $formRow => {
  $formRow.removeClass('form-validate__error');
  $formRow.find('.error').remove();
};


const addFormError = ($formRow, inputLabel, msg) => {
  $formRow.addClass('form-validate__error');
  if (!$formRow.find('.error').length) {
    $formRow.append(`<span class="error">${inputLabel} ${msg}</span>`);
  } else {
    $formRow.find('.error').text(`${inputLabel} ${msg}`);
  }
};



const validateAddressFields = ($target, e) => {
  const form = $target.closest('form');
  let errorCount = 0;

  $(form).find(':input[required]').each(function() {

    const $formRow = $(this).closest('.o-form__row');
    const inputLabel = $(this).attr('title');
    if (!(cheapIsElHidden(this))) {
      // get the length of the input after spaces have been removed, if 0 then apply the errors
      const inputLength = $(this).val().trim().length;
      if (!(inputLength)) {
        errorCount++;
        //check if an error exists or not
        addFormError($formRow, inputLabel, "must not be blank");
      } else {

        if ($(this).attr('data-app') === 'string') {
          if (integerCheck($(this).val()) === false) {
            removeFormErrors($formRow);
          } else {
            errorCount++;
            addFormError($formRow, inputLabel, "must contain characters only");
          }
        }
        if ($(this).attr('data-app') === 'telephone') {
          if (validateTelephone($(this).val()) === false) {
            removeFormErrors($formRow);
          } else {
            errorCount++;
            addFormError($formRow, inputLabel, "must be a valid UK telephone number");
          }
        }
        if ($(this).attr('data-app') === 'pa-postcode') {
          if (validPostcode($(this).val()) === true) {
            removeFormErrors($formRow);
          } else {
            errorCount++;
            addFormError($formRow, inputLabel, "invalid");
          }

        }
        if (errorCount === 0) {
          //remove the error if filled in
          removeFormErrors($formRow);
        }
      }
    }
  });
  if (errorCount != 0) {
    e.preventDefault();

    if ($('.modal-content').is(':visible')) {
      $('.modal-content').animate({
        scrollTop: ($('.form-validate__error').offset() - 100)
      }, 0);
    } else {
      $('html, body').animate({
        scrollTop: ($('.form-validate__error').offset().top - 100)
      }, 0);
    }

  }
};

$('body').on('click', '[data-validate]', function(e) {
  const dataValue = $(this).data('validate');

  if (dataValue === 'typeaheadlength') {
    validateTypeaheadLength($(this), e);
  }

  if (dataValue === 'email') {
    validateEmail($(this), e);
  }

  if (dataValue === 'address') {
    validateAddressFields($(this), e);
  }

});






