  // modal markup generator / modalMarkupGen

  module.exports = function (titleText, id, content, size) {

    console.log('modal build called', titleText, id, content, size);

    var modalSize = '';
    if (size !== undefined) {
      switch (size) {
        case "large":
          modalSize = 'modal--large';
          break;
        case "small":
          modalSize = 'modal--small';
          break;
        default:
          modalSize = '';
      }
    }

    // modal header
    // build modal header
    var modHeader           = $("<div>", {class: "modal-header"});
    var modHeaderButton     = $("<a>", {class: "close", "aria-label": "close", type: 'button', "data-dismiss": "modal"});
    var modHeaderButtonIcon = $("<i>", {class: "icon icon-ui-cross"});
    var modHeaderTitle      = $("<h4>", {class: "modal-title", text: titleText});
    // append modal header
    modHeaderButton.append(modHeaderButtonIcon);
    modHeader.append(modHeaderButton);
    modHeader.append(modHeaderTitle);
    // modal header end


    // modal body
    var modBody = $("<div>", {class: "modal-body", html: content});
    // modal body end


    // modal foot
    var modFoot      = $("<div>", {class: "modal-footer"});
    var modFootClose = $("<button>", {class: "c-btn c-btn--primary u-mar-r-small", "data-dismiss": "modal", text: "Close"});
    // var modFootOK = $("<button>", {class: "c-btn c-btn--secondary", text: "Save changes"});
    modFoot.append(modFootClose);
    // modFoot.append(modFootOK);
    // modal foot end


    // modal wrapper
    var modWrapper = $("<div>", {class: "modal modal--default", id: id});
    var modDialog  = $("<div>", {class: "modal-dialog " + modalSize, role: "document"});
    var modContent = $("<div>", {class: "modal-content"});

    modContent.append(modHeader);
    modContent.append(modBody);
    // disabled footer since we have a sticky header
    modContent.append(modFoot);

    modDialog.append(modContent);
    modWrapper.append(modDialog);

    $('body').append(modWrapper);
  };





