let map;
let mobilemap;
let adjustScroll = true;
let overlayContainer;
const listings = document.getElementById('listings');
const numberOfListings = 10;
const isModal = false;

const svgSpinner = $("<div>", { class: "o_load-spinner o_load-spinner--large" });
const svgSpinnerIcon = require("raw!../../img/load-spinner.svg");
svgSpinner.append(svgSpinnerIcon);


// async script loader
// could move this to a utils module
const loadScript = (src, callback) => {
  const script = document.createElement("script");
  script.type = "text/javascript";
  if (callback) {
    script.onload = callback;
  }
  document.getElementsByTagName("head")[0].appendChild(script);
  script.src = src;
};


// step 1, we'll start async loading of google maps
loadScript('https://maps.googleapis.com/maps/api/js?v=3&key=AIzaSyCIW36t7Ht0DEL6NYUubq74kJjtXKSrseo', initialize);

function initialize() {
  // an array as the first require argument will split the requested code into a chunk
  // automatically split from the main app.js as x.y.js, copied to dist and loaded only when needed
  // perfect for occasional external libraries and big json packets :)

    const GMaps = require("gmaps");
    const clickCollectContain = $('#click-and-collect-contain');
    let isModal = false;
    //const stores = require("json!../../js-data/locations.json");
    // const singleData = require("json!../../js-data/locations-1.json");

    const closeMap = () => {
      clickCollectContain.modal('hide');
    };

    const openMap = () => {
      clickCollectContain.modal('show');
    };

    // Calculates the distance from the current location (clientLatitude1, clientLongitude2) to the data location co-ordinates (dataLatitude1, dataLongitude2)
    const calculateDistance = (clientLatitude1, clientLongitude2, dataLatitude1, dataLongitude2, unit) => {
      const radclientLatitude1 = Math.PI * clientLatitude1 / 180;
      const raddataLatitude1 = Math.PI * dataLatitude1 / 180;
      const radclientLongitude2 = Math.PI * clientLongitude2 / 180;
      const raddataLongitude2 = Math.PI * dataLongitude2 / 180;
      const theta = clientLongitude2 - dataLongitude2;
      const radtheta = Math.PI * theta / 180;
      let dist = Math.sin(radclientLatitude1) * Math.sin(raddataLatitude1) + Math.cos(radclientLatitude1) * Math.cos(raddataLatitude1) * Math.cos(radtheta);

      dist = Math.acos(dist);
      dist = dist * 180 / Math.PI;
      dist = dist * 60 * 1.1515;

      if (unit == "K") {
        dist = dist * 1.609344;
      }

      if (unit == "N") {
        dist = dist * 0.8684;
      }

      return dist;
    };

    // Build the main map
    const generateMap = (stores, longitude, latitude, isModal, isSingleStore) => {
      if (isSingleStore) {
        map = new GMaps({
          div: '#map',
          lat: latitude,
          lng: longitude,
          zoom: 18,
          draggable: true,
          scrollwheel: false,
          disableDoubleClickZoom: true,
          zoomControl: false,
          disableDefaultUI: true
        });
      } else {
        map = new GMaps({
          div: '#map',
          lat: latitude,
          lng: longitude,
          zoom: 12,
          scrollwheel: true,

        });
      }

      loadResults(stores, longitude, latitude, isModal, isSingleStore);


      const width = ($(window).width() - ($('.o-store-locator').offset().left + $('.o-store-locator').outerWidth()));
      $('.js-store-numbers').text(stores.length);
      if (!isSingleStore || typeof isSingleStore === undefined) {
        printResults(stores, isModal);
        if (!isModal) {
          // scroll map & list into view when loaded
          $("html, body").animate({ scrollTop: $('#storeFinderContainer').offset().top }, 0);

        }
      }

      const firstMarker = map.markers[0];

      google.maps.event.trigger(firstMarker, 'click');

      if (width > 0 && !isModal) {
        $('.map-wrapper').css('right', -width);
      } else {
        $('.map-wrapper').css('right', 0);
      }


    };

    // Build the mobile map

    const generateMobileMap = storeId => {

      const mobileData = [],
        result = _.find(stores, o => o.id === storeId);

      mobilemap = new GMaps({
        div: `#map-${storeId}`,
        lat: result.attributes.latitude,
        lng: result.attributes.longitude,
        zoom: 16,
        scrollwheel: false
      });
      mobileData.push(result);

      loadResults(mobileData);
    };

    function map_recenter(latlng, offsetx, offsety) {
      const point1 = map.getProjection().fromLatLngToPoint(
          (latlng instanceof google.maps.LatLng) ? latlng : map.getCenter()
        ),
        point2 = new google.maps.Point(
          ((typeof(offsetx) == 'number' ? offsetx : 0) / Math.pow(2, map.getZoom())) || 0,
          ((typeof(offsety) == 'number' ? offsety : 0) / Math.pow(2, map.getZoom())) || 0
        );
      map.setCenter(map.getProjection().fromPointToLatLng(new google.maps.Point(
        point1.x - point2.x,
        point1.y + point2.y
      )));
    }

    // GetLocation method used for input field and lookup of address / city / postcode
    const GetLocation = (address, isModal, isSingleStore, numberOfListings) => {

      const geocoder = new google.maps.Geocoder();

      geocoder.geocode({ 'address': address }, (results, status) => {
        const parentRow = $('#addressEntry').closest('.o-form__row');
        let trimmedStores;
        $(parentRow).removeClass('form-validate__error');
        $(parentRow).find('.o-form-message--contain').remove();
        if (status == google.maps.GeocoderStatus.OK) {

          $('.o-map-container').show();

          const latitude = results[0].geometry.location.lat(),
            longitude = results[0].geometry.location.lng();

          for (let i = 0; i < stores.length; i++) {
            stores[i].distance = calculateDistance(
              latitude,
              longitude,
              stores[i].attributes.latitude,
              stores[i].attributes.longitude,
              "K");
          }

          stores.sort((a, b) => a.distance - b.distance);
          if (isModal) {
            openMap();
          }
          if (numberOfListings != "all") {
            trimmedStores = stores.slice(0, numberOfListings);
          } else {
            trimmedStores = stores;
          }
          generateMap(trimmedStores, longitude, latitude, isModal, isSingleStore);

        } else {
          $(parentRow).addClass('form-validate__error').append('<div class="o-form-message--contain u-mar-t-small"><label class="o-form__label--message">Please enter a valid address, city or postcode</label></div>');
        }
      });
    };

    // GetLocation method for 'use my location'
    const getClientLocation = (isModal, numberOfListings) => {
      // check for support
      let trimmedStores;
      if (navigator.geolocation) {

        navigator.geolocation.getCurrentPosition(position => {
          $('.o-map-container').show();
          const latitude = position.coords.latitude,
            longitude = position.coords.longitude;

          for (let i = 0; i < stores.length; i++) {
            stores[i].distance = calculateDistance(latitude, longitude, stores[i].attributes.latitude, stores[i].attributes.longitude, "K");
          }

          stores.sort((a, b) => a.distance - b.distance);
          if (isModal) {
            openMap();
          }
          if (numberOfListings != "all") {
            trimmedStores = stores.slice(0, numberOfListings);
          } else {
            trimmedStores = stores;
          }
          generateMap(trimmedStores, longitude, latitude, isModal);
        });
      } else {
        // Browser doesn't support Geolocation
        alert("Browser does not support geolocation");
      }
    };

    var loadResults = (stores, longitude, latitude, isModal, isSingleStore) => {
      const markers_data = [];
      let mapLocatorIcon;
      let icon;
      if (navigator.userAgent.indexOf("MSIE") != -1 || navigator.userAgent.indexOf("Trident") != -1 || navigator.userAgent.indexOf("Firefox") != -1) {
        icon = $("#googleMarkerIcon").text();
        mapLocatorIcon = $("#googleCurrentMarkerIcon").text();
      } else {
        icon = `data:image/svg+xml;utf8',${require("raw!../../img/marker.svg")}`;
        mapLocatorIcon = `data:image/svg+xml;data:image/svg+xml;utf8',${require("raw!../../img/marker-current.svg")}`;
      }

      if (stores.length > 0) {
        const clickMarker = e => {

          const element = $(`[data-marker-index=${e.id}]`);
          setActive(element);
          //
          if (longitude && latitude) {
            const storeElement = `#store-${e.id}`;
            if (!isModal) {

              $('.o-store-locator__locations').animate({

                scrollTop: $(storeElement).position().top
              }, 0);
            }
          }
          const locationIcon = $('.js-use-location').find('i').prop('outerHTML');
          $('.js-use-location').html(`${locationIcon}Use my location`).removeClass('c-loading');
        };

        for (let i = 0; i < stores.length; i++) {

          const item = stores[i];



          if (item.attributes.longitude && item.attributes.latitude !== undefined) {
            if (typeof item.distance == "undefined") {
              markers_data.push({
                lat: item.attributes.latitude,
                lng: item.attributes.longitude,
                title: item.attributes.title,
                id: item.id,
                icon: {
                  url: icon,
                  size: new google.maps.Size(56, 56), // original size you defined in the SVG file
                  scaledSize: new google.maps.Size(56, 56), // desired display size
                },
                infoWindow: {
                  content: `<h4 class="c-info-window__title">${item.attributes.title}</h4>`
                }
              });
            } else {
              markers_data.push({
                lat: item.attributes.latitude,
                lng: item.attributes.longitude,
                title: item.attributes.title,
                id: item.id,
                icon: {
                  url: icon,
                  size: new google.maps.Size(56, 56), // original size you defined in the SVG file
                  scaledSize: new google.maps.Size(56, 56), // desired display size
                },
                infoWindow: {
                  content: `<h4 class="c-info-window__title">${item.attributes.title}</h4><div class="c-info-window__title u-color-pri">${item.distance.toFixed(1)} miles away</div>`
                },
                click: clickMarker
              });

            }

          }
        }
      }

      if (!isSingleStore) {
        if (longitude && latitude) {
          markers_data.push({
            lat: latitude,
            lng: longitude,
            title: 'Your location',
            icon: {
              size: new google.maps.Size(56, 56),
              url: mapLocatorIcon, // original size you defined in the SVG file
              scaledSize: new google.maps.Size(56, 56),
            },
            infoWindow: {
              content: '<h5 class="c-info-window__title">Your location</h5>'
            },
          });
          map.addMarkers(markers_data);
        } else {
          mobilemap.addMarkers(markers_data);
        }
      } else {
        map.addMarkers(markers_data);
      }
    };

    var setActive = el => {
      const siblings = listings.getElementsByTagName('div');
      for (let i = 0; i < siblings.length; i++) {
        siblings[i].className = siblings[i].className
          .replace(/active/, '').replace(/\s\s*$/, '');
      }

      el.find('.o-store-locator__listing-wrapper').addClass('active');
    };

    var printResults = (stores, isModal) => {
      let activeStatus;
      let actionRow;
      let actionButton;
      const resultsForDisplay = document.createElement('div');

      listings.innerHTML = "";

      for (let i = 0, len = stores.length; i < len; i++) {

        const locale = stores[i],
          prop = locale.attributes,
          link = document.createElement('a');

        link.href = '#';
        link.id = `store-${locale.id}`;
        i === 0 ? activeStatus = 'active' : activeStatus = undefined;
        link.className = 'pan-to-marker';
        link.setAttribute("data-marker-index", locale.id);

        resultsForDisplay.appendChild(link);

        const wrapper = document.createElement('div'),
          detailsRow = document.createElement('div'),
          blockTitle = document.createElement('div'),
          distanceTitle = document.createElement('div'),
          clearFix = document.createElement('div'),
          blockAddress = document.createElement('div'),
          tel = document.createElement('div'),
          blockTimes = document.createElement('div'),
          opening = document.createElement('div'),
          callButton = document.createElement('div'),
          openMapButton = document.createElement('div'),
          mapmobile = document.createElement('div'),
          closeMapButton = document.createElement('div'),
          seperator = document.createElement('div');

        wrapper.className = `o-store-locator__listing-wrapper ${activeStatus}`;

        link.appendChild(wrapper);

        detailsRow.className = "row o-store-locator__listings__content u-pad-v-small";

        wrapper.appendChild(detailsRow);

        blockTitle.className = "col-12@xs col-6@md";
        blockTitle.innerHTML = `<div class="o-store-locator__listings__city">${prop.title}</div>`;
        detailsRow.appendChild(blockTitle);

        if (prop.address && typeof locale.distance != "undefined") {
          distanceTitle.className = "col-12@xs col-6@md";
          distanceTitle.innerHTML += `<div class="o-store-locator__listings__distance">${locale.distance.toFixed(1)} miles away</div>`;
          detailsRow.appendChild(distanceTitle);
        }

        clearFix.className = 'u-cf';
        detailsRow.appendChild(clearFix);

        blockAddress.className = 'col-12@xs col-6@lg';
        // blockAddress.innerHTML = '<div class="o-store-locator__listings__city">' + prop.city + '</div>';
        if (prop.address) {
          // blockAddress.innerHTML += '<div class="o-store-locator__listings__distance">' + locale.distance.toFixed(1) + ' miles away</div>';
          blockAddress.innerHTML += `<div class="o-store-locator__listings__address u-mar-t-medium">${prop.address}</div>`;
          // popup += '<div class="quiet">' + prop.address + '</div>';
        }
        if (prop.phone_number) {

          tel.className = 'o-store-locator__listings__phone u-mar-t-medium hidden@sm-down';
          tel.innerHTML = 'Tel: ' + prop.phone_number.replace(/(\d\d\d\d\d)(\d\d\d\d\d\d)/, '$1 $2 ');

          blockAddress.appendChild(tel);
        }

        detailsRow.appendChild(blockAddress);

        blockTimes.className = "col-12@xs col-6@lg";

        opening.className = 'o-store-locator__opening u-mar-t-medium u-font-small-rel';
        opening.innerHTML = `<dl class="detail--vars o-deflist-left o-deflist-left--small"><dt class="u-font-small-rel u-color-dgrey">Monday</dt><dd>${prop.mon_opening_time} - ${prop.mon_closing_time}</dd><dt class="u-font-small-rel u-color-dgrey">Tuesday</dt><dd>${prop.tues_opening_time} - ${prop.tues_closing_time}</dd><dt class="u-font-small-rel u-color-dgrey">Wednesday</dt><dd>${prop.wed_opening_time} - ${prop.wed_closing_time}</dd><dt class="u-font-small-rel u-color-dgrey">Thursday</dt><dd>${prop.thurs_opening_time} - ${prop.thurs_closing_time}</dd><dt class="u-font-small-rel u-color-dgrey">Friday</dt><dd>${prop.fri_opening_time} - ${prop.fri_closing_time}</dd><dt class="u-font-small-rel u-color-dgrey">Saturday</dt><dd>${prop.sat_opening_time} - ${prop.sat_closing_time}</dd><dt class="u-font-small-rel u-color-dgrey">Sunday</dt><dd>${prop.sun_opening_time} - ${prop.sun_closing_time}</dd></dl>`;

        blockTimes.appendChild(opening);
        detailsRow.appendChild(blockTimes);

        if (isModal) {
          actionRow = document.createElement('div');
          actionRow.className = "col-11@xs";

          actionButton = document.createElement('div');
          actionButton.className = 'c-btn c-btn--secondary c-btn--block u-mar-t-medium u-font-upper o-select-store';
          actionButton.setAttribute('data-storeName', prop.address);
          actionButton.setAttribute("data-line-1", prop.address_line_1);
          actionButton.setAttribute("data-line-2", prop.address_line_2);
          actionButton.setAttribute("data-city", prop.city);
          actionButton.setAttribute("data-postcode", prop.postcode);
          actionButton.setAttribute("data-store-ref", prop.reference);
          actionButton.innerHTML = "Select this store";

          actionRow.appendChild(actionButton);
          detailsRow.appendChild(actionRow);

        }

        if (!isModal) {
          actionRow = document.createElement('div');
          actionRow.className = "col-12@xs col-6@lg";

          actionButton = document.createElement('div');
          actionButton.className = 'c-btn c-btn--primary--alt col-10@xs hidden@sm-down u-mar-t-medium u-font-upper';
          actionButton.innerHTML = 'View on Map';

          actionRow.appendChild(actionButton);
          detailsRow.appendChild(actionRow);

        }
        if (prop.phone_number) {
          callButton.className = 'o-store-call hidden@md-up u-cf';
          callButton.innerHTML = '<a href="tel:' + prop.phone_number + '"class="c-btn c-btn--primary col-12@xs u-font-upper u-mar-t-large"><i class="icon icon-telephone u-color-pri u-pad-r-huge icon--vertical-middle"></i>Call store</a>';
        }

        resultsForDisplay.appendChild(callButton);

        openMapButton.className = 'o-store-map-open hidden@md-up u-cf';
        openMapButton.innerHTML = `<a class="c-btn c-btn--primary col-12@xs u-font-upper store-finder__map--mobile-open u-mar-v-large" data-storeId="${locale.id}"><i class="icon icon-menu-storefinder u-color-pri u-pad-r-huge icon--vertical-middle"></i>View Map</a>`;

        resultsForDisplay.appendChild(openMapButton);

        mapmobile.id = `map-${locale.id}`;
        mapmobile.className = 'o-store-map-mobile u-mar-t-large';

        resultsForDisplay.appendChild(mapmobile);

        closeMapButton.className = 'o-store-map-close hidden@md-up u-cf';
        closeMapButton.innerHTML = `<a class="c-btn c-btn--primary col-12@xs u-font-upper store-finder__map--mobile-close u-mar-t-large u-mar-b-small" data-storeId="${locale.id}"><i class="icon icon-menu-storefinder u-color-pri u-pad-r-huge icon--vertical-middle"></i>Close Map</a>`;
        resultsForDisplay.appendChild(closeMapButton);

        seperator.className = 'separator u-mar-b-medium hidden@md-up';

        resultsForDisplay.appendChild(seperator);
      }

      listings.appendChild(resultsForDisplay);
      $(overlayContainer).removeClass('blocking-overlay');
      $(overlayContainer + ' .o_load-spinner').remove();
    };
    if ($('html').hasClass('desktop')) {
      $(window).on('resize', _.debounce(function() {
        $('.o-store-map-mobile').html('').hide();
      }));
    }
    // stores sidelist, click to select, activate, and focus map marker
    $('body').on('click', '.pan-to-marker', function(e) {
      var clicknumber = typeof e.originalEvent.detail == "undefined" ? 0 : e.originalEvent.detail
      if (!isModal && e.originalEvent.detail > 1) {
        e.preventDefault();;
      } else {
        e.preventDefault();
        setActive($(this));

        let position, $index;
        $index = $(this).data('marker-index');

        const result = $.grep(map.markers, e => e.id == $index);

        const marker = result[0];
        position = marker.position;
        marker.infoWindow.open();
        google.maps.event.trigger(marker, 'click');

        map.setCenter(position.lat(), position.lng());
        map.setZoom(12)
      }
    });

    // mobile map open
    $('body').on('click', '.store-finder__map--mobile-open', function(e) {
      $('.o-store-map-close.mapActive a').trigger('click');
      if (!window.isMobile) {
        $(this).parent().prev().prev('a').trigger('click');
      }
      $(this).parent().hide();
      $(this).parent().next().next('.o-store-map-close').show().addClass('mapActive');
      generateMobileMap($(this).data('storeid').toString());
      const currCenter = mobilemap.map.getCenter();
      $(this).parent().next('.o-store-map-mobile').show();
      google.maps.event.trigger(mobilemap.map, 'resize');
      mobilemap.map.setCenter(currCenter);
    });

    // mobile map close
    $('body').on('click', '.store-finder__map--mobile-close', function(e) {
      $(this).parent().prev('.o-store-map-mobile').html('').hide();
      $(this).parent().hide();
      $(this).parent().prev().prev('.o-store-map-open').show();
    });

    // find store by postcode
    $('body').on('click', '.js-find-store', function(e) {
      e.preventDefault();
      isModal = true;
      $('button.js-show-less-stores').removeClass('js-show-less-stores').addClass('js-show-more-stores').text('Show more stores');
      $(this).is("[data-click-collect]") ? isModal = true : isModal = false;
      const address = $('#addressEntry').val();
      GetLocation(address, isModal, '', numberOfListings);
    });

    // find store by current location
    $('body').on('click', '.js-use-location', function(e) {
      const locationIcon = $(this).find('i').prop('outerHTML');
      e.preventDefault();
      isModal = true;
      $('button.js-show-less-stores').removeClass('js-show-less-stores').addClass('js-show-more-stores').text('Show more stores');
      $(this).html(`${locationIcon}Searching`).addClass('c-loading');
      $(this).is("[data-click-collect]") ? isModal = true : isModal = false;
      getClientLocation(isModal, numberOfListings);
    });

    $('body').on('click', '.js-show-more-stores', function(e) {
      e.preventDefault();

      if (isModal) {
        overlayContainer = '.row.o-store-locator';
      } else {
        overlayContainer = '.o-map-container';
      }
      $(overlayContainer).addClass('blocking-overlay');
      $(svgSpinner).clone().appendTo(overlayContainer);
      $(this).removeClass('js-show-more-stores').addClass('js-show-less-stores').text('Show less stores');
      const address = $('#addressEntry').val();
      const numberOfListings = "all";
      adjustScroll = false;
      if (address !== "") {
        GetLocation(address, isModal, '', numberOfListings);
      } else {
        getClientLocation(isModal, numberOfListings);
      }
    });

    $('body').on('click', '.js-show-less-stores', function(e) {
      e.preventDefault();
      if (isModal) {
        overlayContainer = '.row.o-store-locator';
      } else {
        overlayContainer = '.o-map-container';
      }
      $(overlayContainer).addClass('blocking-overlay');
      $(svgSpinner).clone().appendTo(overlayContainer);
      $(this).removeClass('js-show-less-stores').addClass('js-show-more-stores').text('Show more stores');
      const address = $('#addressEntry').val();
      const numberOfListings = 10;
      if (address !== "") {
        GetLocation(address, isModal, '', numberOfListings);
      } else {
        getClientLocation(isModal, numberOfListings);
      }
    });

    // modal method
    $('body').on('click', '.o-select-store', function() {
      const selectedStore = $(this).attr('data-storeName'),
        addressLine1 = $(this).attr('data-line-1'),
        addressLine2 = $(this).attr('data-line-2'),
        addressCity = $(this).attr('data-city'),
        addressPostcode = $(this).attr('data-postcode'),
        storeRef = $(this).attr('data-store-ref');
      $('#checkout-delivery-collect').find('input[data-copy="address-name-hidden"]').val(selectedStore);
      $('#checkout-delivery-collect').find('input[data-copy="address-line1-hidden"]').val(addressLine1);
      $('#checkout-delivery-collect').find('input[data-copy="address-line2-hidden"]').val(addressLine2);
      $('#checkout-delivery-collect').find('input[data-copy="address-city-hidden"]').val(addressCity);
      $('#checkout-delivery-collect').find('input[data-copy="address-postcode-hidden"]').val(addressPostcode);
      $('#checkout-delivery-collect').find('input[data-copy="store_reference-hidden"]').val(storeRef);


      $('.o-store-finder__selected').removeClass('is-hidden');
      $('.o-store-finder__selected [data-ui="candc-address-display"]').text(selectedStore);
      $(".o-submit-button").show();
      closeMap();

    });

    // resize map on window resize, keeps dimensions sweet
    if ($('.map-wrapper').length > 0) {
      $(window).on('resize', _.debounce(() => {
        const width = ($(window).width() - ($('.o-store-locator').offset().left + $('.o-store-locator').outerWidth()));
        if (width > 0 && !isModal) {
          $('.map-wrapper').css('right', -width);
        } else {
          $('.map-wrapper').css('right', 0);
        }
      }));
    }
}



