const readCookie = name => (name = new RegExp(`(?:^|;\\s*)${('' + name).replace(/[-[\]{}()*+?.,\\^$|#\s]/g, '\\$&')}=([^;]*)`).exec(document.cookie)) && name[1];
// console.log(readCookie('globalHeader'));
if (readCookie('globalHeader') != "hidden") {
    $('#o-super-header__container').show();
}

if (readCookie('matalanUsername')) {
    $('[data-remember="email"]').val(readCookie('matalanUsername'));
    $('[data-remember="checkbox"]').prop('checked', true);
}

$(() => {

  if (readCookie('_matalan_product_view') === "wide") {
      $('.js-results-wide').trigger('click');
  }

  if (readCookie('_matalan_product_view') === "tight") {
      $('.js-results-tight').trigger('click');
  }

});

$(document).on('pjax:complete', e => {
  //needs to fire only when on the plp page.
  if (readCookie('_matalan_product_view') === "wide") {
      $('.js-results-wide').trigger('click');
  }

  if (readCookie('_matalan_product_view') === "tight") {
      $('.js-results-tight').trigger('click');
  }

});


