$('select.js-product-select--size').on('change', function (e) {

  const $this = $(this);
  const $thisProduct = $this.closest( $('[data-app^=productrow-id]') );
  const $thisEwisPanel = $thisProduct.find( $('[data-app=pdp__ewis__panel]') );
  const $thisSlick = $this.closest( $('.o-product-suite__slick') );

  $thisEwisPanel.addClass('is-hidden');
  $thisEwisPanel.find( $('.o-list__tick') ).html('');
  $thisProduct.find( $('.js-ewis-hide') ).removeClass('is-hidden');

  const selectedOption = $(this).val();
  $('.js-ewis-variant').val(selectedOption.replace('ewis-', ''));

  if (selectedOption.toLowerCase().indexOf('ewis') >= 0) {
    $thisProduct.find( $('.js-ewis-hide') ).addClass('is-hidden');
    $thisEwisPanel.removeClass('is-hidden');
  } else {
    $thisProduct.find( $('.js-ewis-hide') ).removeClass('is-hidden');
    $thisEwisPanel.addClass('is-hidden');
  }

  // force a refresh of slicks adaptive height
  $thisSlick.slick( 'slickSetOption', 'adaptiveHeight', true, true );

});


