require("../vendors/jquery.pjax");

var svgSpinner = $("<div>", { class: "o_load-spinner o_load-spinner--large" });
var svgSpinnerIcon = require("raw!../../img/load-spinner.svg");
svgSpinner.append(svgSpinnerIcon);

var $body = $('body');


$.pjax.defaults.timeout = 0;
$.pjax.defaults.scrollTo = false;
$.pjax.defaults.fragment = '[data-pjax-container]';


$(document).on('pjax:send', function(e) {
  console.log('pjax start');
  $('[data-pjax-container]').addClass('blocking-overlay');
  $(svgSpinner).clone().appendTo('[data-pjax-container]');
});

$(document).on('pjax:complete', function(e) {
  console.log('pjax complete');
  $('[data-pjax-container]').removeClass('blocking-overlay');
  $('[data-pjax-container] .o_load-spinner').remove();
});



