import { $http } from './ajax-promise';
const $minibagContain = $('#minibag-contain');
const iconOkay = '<i class="icon icon-small-tick icon--vertical-left"></i>';
const svgSpinner = $("<div>", { class: "o_load-spinner" });
import svgSpinnerIcon from "raw!../../../img/load-spinner.svg";
svgSpinner.append(svgSpinnerIcon);

// catch clicks on any regular element tagged as such
$('body').on('click', 'a[data-ajax-ui], button[data-ajax-ui]', function(e) {
  e.preventDefault();
  handleClick.call(this);
});

// select box will never receive a click, listen for change instead
$('body').on('change', 'select[data-ajax-ui]', function(e) {
  e.preventDefault();
  handleClick.call(this);
});

// form change - promo code entry OR feedback to user
$('body').on('submit', 'form[data-ajax-ui="form"], form[data-ajax-ui="message"]', function(e) {
  e.preventDefault();
  handleClick.call(this);
});

// catch clicks on any regular element tagged as such
$('body').on('click', 'input[type=radio][data-ajax-ui]', function(e) {
  handleClick.call(this);
});



function handleClick() {
  const $this = $(this);
  const ui = $this.data('ajax-ui');
  const $parentForm = getParentForm($this);
  const $parentRow = $this.closest('.o-form__row');

  // if we're not a child of a form, this isn't going to work
  if ($this.data('ajax') && $this.closest('form').length > 0) {

    //
    // RADIO METHOD - WE NEED TO HANDLE SIBLINGS
    //
    if (ui === 'radio') {
      const $siblings = getSiblings($this.data('siblings'));
      // lock the form, disable the siblings
      handleFeedbackOn($this, $siblings, ui);
      // make the call
      ajaxFormCall($this, handleFeedbackOff.call($this, $siblings, ui));
    }

    //
    // REMOVE ITEM METHOD - SUBMITS LINE ITEM CHANGE WITHOUT QTY
    //
    if (ui === 'lineitem_remove') {
      handleFeedbackOn($this, $parentForm, ui);
      // disable the select qty, this will delete the line item on submission
      $parentForm.find('select').prop('disabled', true);
      // make the call
      ajaxFormCall($this, handleFeedbackOff.bind(this, $parentForm, ui));
    }

    //
    // ADD TO BAG METHOD
    //
    if (ui === 'lineitem_add') {
      handleFeedbackOn($this, $this.parent(), ui);
      ajaxFormCall($this, handleFeedbackOff.bind(this, $this.parent(), ui));
    }

    //
    // QUANTITY UPDATE or FORM ACTION (eg. mybag delivery dropdown)
    //
    if (ui === 'lineitem_quantity' || ui === 'form') {
      handleFeedbackOn($this, $parentForm, ui);
      ajaxFormCall($this, handleFeedbackOff.bind(this, $parentForm, ui));
    }

    //
    // FORM CHANGE (submit & display response inline)
    //
    if (ui === 'message') {
      handleFeedbackOn($this, $this, ui);
      ajaxFormCall($this, handleFeedbackOff.bind(this, $this, ui));
    }

    //
    // DELIVERY METHOD - NEEDS TO KEEP SIDEBAR & MINIBAG DELIVERY PRICE IN SYNC
    //
    if (ui === 'delivery_method') {
      const $radios = $parentRow.find('input[type=radio]');
      const $newChkd = $(this);
      let $preChkd;
      // no change to selection, abort
      if ($newChkd.attr('checked') === 'checked') {
        return false;
      }
      // need to step through attr here, prop misbehaves inside this event
      _.forEach($radios, o => {
        if ($(o).attr('checked') === 'checked') {
          $preChkd = $(o);
          $(o).attr('checked', false);
        }
      });
      $newChkd.attr('checked', true);

      if ($this.closest('#deliveryAddressAccord').length) {
        // part of checkout 2, special handling
        if ($newChkd.val() === 'click_and_collect' || ($newChkd.data('delivery-id') !== $preChkd.data('delivery-id'))) {
          handleFeedbackOn($this, $parentRow, ui);
          ajaxDeliverySet($this, handleFeedbackOff.bind($this, null, ui));
        }
      } else {
        handleFeedbackOn($this, $parentRow, ui);
        ajaxDeliverySet($this, handleFeedbackOff.bind($this, null, ui));
      }
    }
  }
}



function handleFeedbackOn($this, $els, ui) {

  if (ui === 'lineitem_add') {
    if (checkFormRequiredWithDialog(getParentForm($this))) {
      $els.addClass('blocking-overlay');
      $(svgSpinner).clone().appendTo($els);
    }
  }

  if (ui === 'lineitem_remove' ||
    ui === 'lineitem_quantity' ||
    ui === 'form' ||
    ui === 'message' ||
    ui === 'delivery_method') {

    $els.addClass('blocking-overlay');
    $(svgSpinner).clone().appendTo($els);
  }

  if (ui === 'radio') {
    
    
    if($this.data('action') === 'set_preferred_billing') {
      $('.panel.preferred-billing').removeData('preferred-billing').removeClass('preferred-billing');
      $this.closest('.panel').data('preferred-billing', true);
      $this.closest('.panel').addClass('preferred-billing');
    }
    if($this.data('action') === 'set_preferred_shipping') {
      $('.panel.preferred-shipping').removeData('preferred-shipping').removeClass('preferred-shipping');
      $this.closest('.panel').data('preferred-shipping', true);
      $this.closest('.panel').addClass('preferred-shipping');
    }
    // fake radio button behaviour, disable all siblings
    _.forEach($els, (o, i) => {
      $(o).prop('disabled', true);
    });
    $(svgSpinner).clone().appendTo($this);
  }
}



function handleFeedbackOff($els, ui) {
  const $this = $(this);

  if (ui === 'lineitem_add' ||
    ui === 'lineitem_quantity' ||
    ui === 'form' ||
    ui === 'message' ||
    ui === 'delivery_method' ||
    ui === 'lineitem_remove') {
    $this.closest('.blocking-overlay').removeClass('blocking-overlay').find('.o_load-spinner').remove();

  } else if (ui === 'radio') {
    // TODO - tidy this to use attributes
    const $currentDefault = $(`p[data-siblings="${$this.data('siblings')}"]`).not('.is-hidden');
    
    setTimeout(() => {      
      $currentDefault.addClass('is-hidden').next('form.is-hidden').removeClass('is-hidden');
      getParentForm($this).addClass('is-hidden').prev('p.is-hidden').removeClass('is-hidden');
      $els.find('.o_load-spinner').remove();
      _.forEach($els, (o) => {
        $(o).prop('disabled', false).removeClass('disabled');
      });
    }, 100);
  }
}



function uiUpdate($this, dataSrc) {
  const ui = $this.data('ajax-ui');
  let data;
  dataSrc.hasOwnProperty('data') ? data = dataSrc.data : data = dataSrc;
  console.warn(data);

  // UPDATE HEADER NUMERIC / TEXT NUMBER OF ITEMS
  //
  if ($('[data-ui="bag-linecount__linetotal"]').length || $('[data-ui="bag-linecount__numtotal"]').length) {

    if (ui === 'lineitem_add' ||
      ui === 'lineitem_remove' ||
      ui === 'lineitem_quantity') {

      // update numeric total (minibag button numbers)
      _.forEach($('[data-ui="bag-linecount__numtotal"]'), (o) => {
        $(o).text(data.attributes.total_quantity);
      });
      // update text total (x items in your bag)
      _.forEach($('[data-ui="bag-linecount__linetotal"]'), (o) => {
        $(o).text(data.attributes.total_quantity + (data.attributes.total_quantity > 1 ? ' items ' : ' item '));
      });

    }
  }

  //
  // UPDATE LIST FOOTERS
  //
  if (ui === 'lineitem_add' ||
    ui === 'lineitem_remove' ||
    ui === 'lineitem_quantity' ||
    ui === 'delivery_method' ||
    ui === 'form') {

    const newLineItemTotalTotals = {
      "bag-linecount__subtotal": displayBagSubtotal(data),
      "bag-linecount__saving": displayTotalSavings(data),
      "bag-linecount__shipping": displayDeliveryPrice(data),
      "bag-linecount__discounts": displayActiveCoupons(data)
    };

    _.forEach(newLineItemTotalTotals, (v, k) => {
      $(`[data-ui="${k}"]`).html(v);
    });
  }

  //
  // HANDLE PROMO CODE RESPONSE
  //
  if ($this.data('ajax-ui') === 'form' && $this.data('action') === 'promocode') {
    (() => {
      const couponData = data.coupon;
      const $resultContain = $this.find($('[data-role=promotion_message]'));
      let result;

      if (couponData.hasOwnProperty('error')) {
        result = $("<li>", {
          class: "error",
          html: `Error adding the promo code <strong>${couponData.coupon_code}</strong><br>${couponData.error}`
        });
      } else if (couponData.hasOwnProperty('success')) {
        result = $("<li>", {
          class: "success",
          html: `You have successfully added the promo code <strong>${couponData.coupon_code}</strong>`
        });
      }
      $resultContain.html(result);
    })();
  }

  //
  // GENERIC MESSAGE FEEDBACK, CURRENTLY ON EWIS
  //
  if ($this.data('ajax-ui') === 'message') {
    (() => {
      const $resultContain = $this.find($('[data-role=user_message]'));
      let result;

      if (data.status === 'error') {
        result = $("<li>", {
          class: "error",
          html: `<strong>${data.message}</strong>`
        });
      }
      if (data.status === 'success') {
        result = $("<li>", {
          class: "success",
          html: `<strong>${data.message}</strong>`
        });
      }
      $resultContain.html(result);
      $this.closest('.o-product-suite__slick').slick( 'slickSetOption', 'adaptiveHeight', true, true );
    })();
  }
  //
  // REBUILD THE MINIBAG
  //
  if (ui === 'lineitem_add' ||
    ui === 'lineitem_remove' ||
    ui === 'lineitem_quantity' ||
    (ui === 'form' && $this.data('action') === 'promocode')) {

    // if there's a bag on the page
    if ($('[data-ui="bag-lineitems"]').length) {
      // deinit the slick to support new items
      deInitAjaxSlick($('[data-ui="bag-lineitems"]'));
      // clear the styled selectboxes
      $('[data-ui="bag-lineitems"]').find('select').selectBox('destroy');
      // inject the new markup
      templateGenerate(data);

      // // if its the minibag, open it for 3 seconds
      if ($minibagContain.length && ui === 'lineitem_add') {
        let bagAddEvent;

        try {
          if ($.browser.ie && $.browser.major == 11) {
            bagAddEvent = new CustomEvent("minibag.lineitem_added");
            bagAddEvent.initCustomEvent("minibag.lineitem_added", true, false, null);
          } else {
            bagAddEvent = new Event('minibag.lineitem_added');
          }
          // we'll pick this event up in minibag.js
          document.dispatchEvent(bagAddEvent);
        } catch (e) {
          console.log(e);
        };
      }
    }
  }

  //
  // ENABLE / DISABLE THE CHECKOUT BUTTON
  //
  if (ui === 'lineitem_remove' || ui === 'lineitem_add' || ui === 'lineitem_quantity') {

    var lineErrors = [];
    var outOfStock = false;
    _.forEach(data.line_items, function(o, i) {
      if (!_.isEmpty(o.errors)) {
        lineErrors.push(o.errors);
      }

      if (o.stock_available_level < o.unit_quantity) {
        outOfStock = true;
      }
    });

    if (lineErrors.length > 0 || data.attributes.total_quantity < 1 || outOfStock === true) {
      $('[data-ui="bag-checkout"]').addClass('disabled');
    } else {
      $('[data-ui="bag-checkout"]').removeClass('disabled');
    }

    if (lineErrors.length < 1) {
      $('[data-ui="bag-itemerror"]').addClass('is-hidden');
    }

    if (data.attributes.total_quantity === 0) {
      // clear any els we don't need for an empty bag
      $('[data-emptybag-remove]').addClass('is-hidden');
      // clear any inputs e.g. promo code if one has been applied
      $('[data-emptybag-clear]').val('');
      // show the empty bag notice
      $('[data-ui="bag-emptybasket"]').removeClass('is-hidden');

    } else if (data.attributes.total_quantity >= 1) {
      // otherwise find any disabled checkout buttons and re-enable
      $('[data-ui="bag-emptybasket"]').addClass('is-hidden');
      $('[data-emptybag-remove].is-hidden').removeClass('is-hidden');
    }
  }

  //
  // FREE DELIVERY TEXT READOUT IN THE MINIBAG
  //
  if (ui === 'lineitem_remove' ||
    ui === 'lineitem_add' ||
    ui === 'lineitem_quantity' ||
    (ui === 'form' && $this.data('action') === 'promocode')) {

    // start minibag
    if (data.attributes.free_delivery_away_sum === "£0.00") {
      $('[data-ui="bag-shipping__qualify__msg"]').text('You have qualified for ');
    } else {
      const shippingQualifyDiff = data.attributes.free_delivery_away_sum;
      const shippingQualifyMarkup = $("<span>", { class: "u-font-wb u-color-pri" });

      $(shippingQualifyMarkup).html(shippingQualifyDiff);
      $('[data-ui=bag-shipping__qualify__msg]').html(`You are ${$(shippingQualifyMarkup).prop('outerHTML')} away from qualifying for `);
    }
    // end minibag

    // mybag delivery dropdown needs updating
    if ($('[data-ui=bag-shipping-option]').length) {
      const sel = $('[data-ui=bag-shipping-option]')[0];
      // findIndex to pluck standard delivery price from data, just incase it moves again
      const stdDeliveryIndex = _.findIndex(data.shipping_methods, ship => ship.sku == 'REGULAR');

      sel.options[stdDeliveryIndex].text = `Standard (${getDeliveryPrice(data, 'REGULAR')})`;
      $(sel).selectBox('refresh');
    }

    // checkout 3 standard delivery inline label needs updating
    if ($('[data-ui=inline--shipping__standard]').length) {
      $('[data-ui=inline--shipping__standard]').text(getDeliveryPrice(data, 'REGULAR'));
    }
  }

  //
  // DELIVERY CHANGE - update the sidebar shipping cost
  //
  if (ui === 'delivery_method') {
    if ($('[data-ui=bag-linecount__shipping]').length) {
      $('[data-ui=bag-linecount__shipping] dd').text(getCurrDeliveryPrice(data));
    }
  }

  //
  // LINE_ITEM ADD - watch for max items exceeded errors in returned JSON response
  //
  if (ui === 'lineitem_add') {
    if (data.hasOwnProperty('error_too_many_line_items')) {
      console.warn('error_too_many_line_items', data.error_too_many_line_items.error);
      $('[data-ui=bag-lineitem__error]').text(data.error_too_many_line_items.error).removeClass('is-hidden');
    } else {
      $('[data-ui=bag-lineitem__error]:not(.is-hidden)').text('').addClass('is-hidden');
    }
  }

  //
  // LINE_ITEM REMOVE
  //
  if (ui === 'lineitem_remove') {
    $(`[data-product="${$this.closest('[data-product]').data('product')}"]`).fadeOut(250, function() {
      $(this).remove();
    });
  }
}



function checkFormRequiredWithDialog($this) {
  $this.find($('[required]')).each(function() {
    if ($(this).val() === '') {
      createDialog.show({
        title: 'Error',
        size: 'size-small',
        message: $(this).data('required-error'),
        closable: true,
        buttons: [{
          label: 'OK',
          cssClass: 'c-btn c-btn--primary',
          action(dialogRef) {
            dialogRef.close();
          }
        }]
      });
      const error = $(this).data('required-error') ? $(this).data('required-error') : '';
      throw new Error(error);
    }
  });
  return true;
}


const ajaxDeliverySet = ($this, callback) => {
  const authToken = $this.closest('form').find('[name=authenticity_token]').val();

  let deliveryValue;
  if ($this.data('delivery-id') && !isNaN($this.data('delivery-id'))) {
    deliveryValue = $this.data('delivery-id');
  } else if (!isNaN($this.val())) {
    deliveryValue = $this.val();
  } else {
    throw 'error, unable to get delivery type ID';
  }

  const formData = {
    'utf8': '✓',
    '_method': 'patch',
    'authenticity_token': authToken,
    'cart[shipping_method_id]': deliveryValue,
    'site_password': 'batman'
  };

  $http('/cart.json')
    .patch(formData)
    .then(data => { uiUpdate($this, JSON.parse(data)); })
    .then(callback)
    .catch(cb.error);
};


var ajaxFormCall = ($this, callback) => {
  const $parentForm = getParentForm($this);
  const formData = $parentForm.serialize();

  let destination = $parentForm.attr('action');
  // forcing all requests to be suffixed with .json
  destination = destination.match(/.json$/) ? destination : destination += '.json';

  let formType;
  if ($parentForm.find('input[name=_method]').length) {
    // if we have a _method in place, honour it
    formType = $parentForm.find('input[name=_method]').val();
  } else {
    // else use the parent form declaration
    formType = $parentForm.attr('method');
  }

  $http(destination)[formType](formData)
    .then(data => { uiUpdate($this, JSON.parse(data)); })
    .catch(err => {
      // there was a problem, undo the form changes
      $parentForm.get(0).reset();
      // and reinit custom selects to match
      $parentForm.find('select').selectBox('refresh');
      cb.error(err);
    })
    .then(callback);
};


var cb = {
  success(data) {
    console.log(1, 'success', JSON.parse(data));
  },
  error(data) {
    console.warn(2, 'error', data);

  }
};

//
// HELPERS
//
function formatAsCurr(n, currency) {
  if (isNaN(n)) {
    n = Number(n.replace(/[^0-9\.]+/g, ""));
  }
  if (typeof n === 'string') {
    n = Number(n);
  }
  return currency + n.toFixed(2).replace(/./g, (c, i, a) => i > 0 && c !== "." && (a.length - i) % 3 === 0 ? "," + c : c);
}

function formatAsNum(n) {
  if (isNaN(n)) {
    n = Number(n.replace(/[^0-9\.]+/g, ""));
  }
  if (typeof n === 'string') {
    n = Number(n);
  }
  return n;
}

function getSiblings(sib) {
  return $('body').find(`[data-siblings="${sib}"]`);
}

function getParentForm($this) {
  return $($this.closest('form'));
}

function reInitAjaxSelect(el) {
  el.find('select').selectBox({
    mobile: false,
    loopOptions: true
  });
}

// slick needs a re-init after modifying its contents
function deInitAjaxSlick(el) {
  const $minibagSlickDef = $('#minibag-contain .o-bag--list-contain');
  $('.js-minibag--checkout').removeClass('js-minibag-init');
  if ($minibagSlickDef.hasClass('slick-initialized')) {
    $minibagSlickDef.slick('destroy');
  }
}

function getDeliveryPrice(data, sku) {
  const stdDeliveryMethod = _.findIndex(data.shipping_methods, ship => ship.sku == sku);
  if (stdDeliveryMethod < 0) {
    throw new Error(`unable to determine requested delivery pricing for ${sku}`);
  }
  return data.shipping_methods[stdDeliveryMethod].total;
}

function getDeliveryLabel(data, sku) {
  const stdDeliveryMethod = _.findIndex(data.shipping_methods, ship => ship.sku == sku);
  if (stdDeliveryMethod < 0) {
    throw new Error(`unable to determine requested delivery label for ${sku}`);
  }
  let append;
  sku === 'EXPRESS_2_DAY' ? append = '' : append = ' Delivery';
  return data.shipping_methods[stdDeliveryMethod].label + append;
}

// get the current chosen delivery price from the server response
function getCurrDeliveryPrice(data) {
  const chosenDeliveryIndex = _.findIndex(data.shipping_methods, ship => ship.id == data.attributes.shipping_method_id);
  if (chosenDeliveryIndex < 0) {
    console.warn('unable to determine current delivery method, using c&c');
    return getDeliveryPrice(data, 'EXPRESS_2_DAY');
  }
  return data.shipping_methods[chosenDeliveryIndex].total;
}

// get the current chosen delivery price from the server response
function getCurrDeliveryLabel(data) {
  const chosenDeliveryIndex = _.findIndex(data.shipping_methods, ship => ship.id == data.attributes.shipping_method_id);
  if (chosenDeliveryIndex < 0) {
    console.warn('unable to determine current delivery method, using c&c');
    return getDeliveryLabel(data, 'EXPRESS_2_DAY');
  }
  if (data.shipping_methods[chosenDeliveryIndex].sku === "EXPRESS_2_DAY") {
    return data.shipping_methods[chosenDeliveryIndex].label;
  } else {
    return `${data.shipping_methods[chosenDeliveryIndex].label} Delivery`;
  }
}

function displayDeliveryPrice(data) {
  if (data.attributes.total_quantity < 1) {
    return false;
  } else {
    const markup = [];
    markup.push($("<dt>", { html: getCurrDeliveryLabel(data) }));
    markup.push($("<dd>", { html: getCurrDeliveryPrice(data) }));
    return markup;
  }
}

function displayTotalSavings(data) {
  if (data.attributes.total_discount === '£0.00') {
    return false;
  } else {
    const markup = [];
    markup.push($("<dt>", { html: $('[data-ui=bag-linecount__saving]').data('ui-label') }));
    markup.push($("<dd>", { html: `&minus;${data.attributes.total_discount}` }));
    return markup;
  }
}

function displayBagSubtotal(data) {
  if (data.attributes.total_quantity < 1) {
    return "£0.00";
  } else {
    return data.attributes.total;
  }
}

function displayActiveCoupons(data) {
  const markup = [];
  $.each(data.discount_summaries, function() {
    markup.push($("<dt>", { class: "u-color-pri", html: this.title }));
    markup.push($("<dd>", { class: "u-color-pri", html: `&minus;${this.total}` }));
  });
  return markup;
}

function calcLineItemSavings() {
  if (this.original_unit_price != this.unit_price) {
    return formatAsCurr((formatAsNum(this.original_unit_price) - formatAsNum(this.unit_price)) * this.unit_quantity, '£');
  }
}

function templateGenerate(data) {
  // wrangle the data where needed and add it in with the line_item
  // _sku_lower - for URLs
  // _lineitem_saving - calc markdown savings where req'd
  _.forEach(data.line_items, o => {
    Object.assign(o, {
      _sku_lower: o.sku.toLowerCase(),
      _lineitem_saving: calcLineItemSavings.call(o)
    });
  });

  // Use custom template delimiters - erb style by default, handlebars style in use below
  _.templateSettings.interpolate = /{{=([\s\S]+?)}}/g;
  _.templateSettings.escape = /{{-([\s\S]+?)}}/g;
  _.templateSettings.evaluate = /{{([\s\S]+?)}}/g;
  // data namespace
  _.templateSettings.variable = "lineItems";

  const cartContainers = ['data-ui=itemtable-template', 'data-ui=minibag-template'];

  _.forEach(cartContainers, o => {
    // Grab the HTML out of our template tag and pre-compile it.
    if ($(`[${o}]`).length) {
      const templateMarkup = _.template($(`[${o}]`).html());
      $(`[${o}--hook]`).html('').append(
        templateMarkup(data.line_items)
      ).find('select').selectBox({
        mobile: false,
        loopOptions: true
      });
    }
  });

  // duplicate of mininbag.js options, to be centralised
  const verticalMinibagSlickOptions = {
    vertical: true,
    infinite: false,
    slidesToShow: 2,
    slidesToScroll: 2,
    prevArrow: '<button type="button" class="c-btn c-btn--primary c-btn--tiny slick-prev">' +
      '<i class="icon--block icon icon-ui-up"></i></button>',
    nextArrow: '<button type="button" class="c-btn c-btn--primary c-btn--tiny slick-next">' +
      '<i class="icon--block icon icon-ui-down"></i></button>'
  };

  if (data.attributes.line_items_count > 2 && !window.isMobileOrTablet) {
    $('.modal--minibag [data-ui=minibag-template--hook]').slick(verticalMinibagSlickOptions);
    $('.js-minibag--checkout').addClass('js-minibag-init');
  }

}



