module.exports = function (url, extractContent, appendContentTo) {

   console.log('ajax called', url, appendContentTo);

   $.ajax({
      url: url,
      headers: {
         'X-PJAX': true
      },
      success: function(data) {

        // data = $($.parseHTML(data)).filter('#sizeguide');

        data = $(data).find(extractContent);

         $(appendContentTo).html( data );
      },
      always: function(data) {
         console.log('ajax always', data);
      },
      error: function() {
         console.log('ajax error');
      }
   });

}


