(function($) {
  var offCanvas = {

    _init: function(element) {
      this.$body = $('body');
      this.$parentNavMenu = $('[data-app="accessible-navmenu"]');
      this.$navContain = $('#nav-contain');
      this.$topMenuLevel = $('.navbar__top-item > .link');
      this.$subMenuLevel = $('.sub-nav-title');
    },

    _modalEventsOn: function() {
      this.$navContain.on('show.bs.modal', function(e) {
        this.$parentNavMenu.addClass("active");
        this.$body.addClass("modal-open--nav");
      }.bind(this));

      this.$navContain.on('hide.bs.modal', function(e) {
        this.$parentNavMenu.removeClass("active");
        this.$body.removeClass("modal-open--nav");
      }.bind(this));
    },

    _modalEventsOff: function() {
      this.$navContain.off('.bs.modal');
    },

    start: function() {
      this._init();
      this._modalEventsOn();

      this.$topMenuLevel.on('click.nav-offcanvas', function(e) {
        e.preventDefault();
        if (!$(e.target).hasClass('open')) {
          // if this isn't already open, close any open siblings before proceeding
          this.$parentNavMenu.find('.open').removeClass('open');
        }
        $(e.target).toggleClass('open').next('.sub-nav').toggleClass('open');
      }.bind(this));

      // toggle second level (sub nav) item
      this.$subMenuLevel.on('click.nav-offcanvas', function(e) {
        e.preventDefault();
        if (!$(this).hasClass('open')) {
          // if this isn't already open, close any open siblings before proceeding
          $(this).closest('.sub-nav').find('.open').removeClass('open');
        }
        $(this).find('.link').toggleClass('open');
        $(this).toggleClass('open').nextUntil('.sub-nav-title').toggleClass('open');
      });

      // open menu item chevron click - href handling to go here
      $('[data-ui-action="fakelink"]').on('click.nav-offcanvas', function(e) {
        e.stopPropagation();
        window.location.assign($(this).data('href'));
      });
    },

    stop: function() {
      this.$topMenuLevel.off('.nav-offcanvas');
      this.$subMenuLevel.off('.nav-offcanvas');
      this._modalEventsOff();
    }
  };

  module.exports = {
    start: offCanvas.start.bind(offCanvas),
    stop: offCanvas.stop.bind(offCanvas),
  };

})($);


