// quick function to detect:
// 1/ if a hash is present
// 2/ and if that hash is associated with a tab
// 3/ trigger a click on it

if (location.hash !== null && location.hash !== "") {
  var hash = location.hash;
  var $target = $(hash);

  if ($target.attr('role') === 'tabpanel') {

    var $currentTabs = $('[role="tab"]');

    $currentTabs.each(function() {
      if ($(this).attr('href') === hash) {
        $(this).trigger('click');
      }

      // override the default scroll to hash behaviour
      // no events to plug in to so we'll hack with a 0 timeout to push it down the stack
      setTimeout(function() {
        $('body, html').scrollTop(0);
      }, 0);

    });
  }
}



