(function() {
  'use strict';

  var miniBagControls = '.js-minibag--checkout, .js-minibag--view';
  var $miniBagControls = $(miniBagControls);
  var $miniBagContain = $('#minibag-contain');
  var modalBG = '.modal-backdrop';
  var $body = $('body');
  var minibagHoverTimeout = 250;
  var minibagHoverTimer;
  var $minibagSlickDef = $('#minibag-contain .o-bag--list-contain');

  // ===========================
  // Slick Slider initialisation
  // ===========================
  require("../vendors/slick");

  var verticalMinibagSlickOptions = {
    vertical: true,
    infinite: false,
    slidesToShow: 2,
    slidesToScroll: 2,
    prevArrow: '<button type="button" class="c-btn c-btn--primary c-btn--tiny slick-prev">' +
               '<i class="icon--block icon icon-ui-up"></i></button>',
    nextArrow: '<button type="button" class="c-btn c-btn--primary c-btn--tiny slick-next">' +
               '<i class="icon--block icon icon-ui-down"></i></button>'
  };



  function initialiseMinibagSlick() {
    if ($minibagSlickDef.hasClass('slick-vertical') && !window.isMobileOrTablet) {
      // previously loaded, reinit
      $minibagSlickDef.slick('refresh');

    } else {
      // first load, init
      if ($minibagSlickDef.children().length > 2 && !window.isMobileOrTablet) {
        $minibagSlickDef.not('.slick-initialized').slick(verticalMinibagSlickOptions);
        $('.js-minibag--checkout').addClass('js-minibag-init');
      }
    }
  }


  // ===========================
  // Slick Slider End
  // ===========================



  // =================================
  // Modal Show / Hide Behaviour Hooks
  // =================================

  function closeMiniBag() {
    $miniBagContain.modal('hide');
  }

  function openMiniBag() {
    // already open, abort
    if ($body.hasClass('modal-open')) return;
    $miniBagContain.modal('show');
  }

  $miniBagContain.on('show.bs.modal', function() {
    $body.addClass('modal-open--minibag');
  });

  $miniBagContain.on('shown.bs.modal', function() {
    initialiseMinibagSlick();
  });

  $miniBagContain.on('hide.bs.modal', function() {
     if ( $('.js-minibag--checkout').hasClass('js-minibag-init') ) {
      $minibagSlickDef.slick('unslick');
     }
    $body.removeClass('modal-open--minibag');
  });



  // =================================
  // Modal Show / Hide Event Logic
  // =================================

  // enable the events on load
  minibagMouseEvents('on', miniBagControls, openMiniBag);
  minibagMouseEvents('on', modalBG, closeMiniBag);

  // close on click of modal background
  $(document).on('click', modalBG, function() {
    closeMiniBag();
    clearTimeout(minibagHoverTimer);
  });


  document.addEventListener('minibag.lineitem_added', function() {

    openMiniBag();
    minibagMouseEvents('off', miniBagControls, openMiniBag);
    minibagMouseEvents('off', modalBG, closeMiniBag);

    setTimeout(function() {
      minibagMouseEvents('on', miniBagControls, openMiniBag);
      minibagMouseEvents('on', modalBG, closeMiniBag);
      // if we're hovering over the minibag after 3 secs, abort the close
      if ($('#minibag-contain').is(":hover")) return;
      // FE-081 / artf4841084 - don't auto close when on tablet
      else if (!window.matchMedia("(min-width: 720px) and (max-width: 1024px)").matches) {
        closeMiniBag();
      }
    }, 3000);

  }, false);


  function minibagMouseEvents(type, el, func) {
    if (type === 'on') {
      $(document)
        .on('mouseover.minibag', el, function() {
          minibagHoverTimer = setTimeout(function() {
            func();
          }, minibagHoverTimeout);
        })
        .on('mouseout.minibag', el, function() {
          clearTimeout(minibagHoverTimer);
        });

    } else {
      $(document)
        .off('.minibag', el);
    }
  }

})();



