(function($) {
  'use strict';

  // minor JS rules for handling of states in the UI kit
  // we'll need these still, investigate creating JS patterns we can re-use

  function validateEmail(email) {
    var re = /^([\w-]+(?:\.[\w-]+)*)@((?:[\w-]+\.)*\w[\w-]{0,66})\.([a-z]{2,6}(?:\.[a-z]{2})?)$/i;
    return re.test(email);
  }

  // results image hover function
  // swaps the src and data-src image URLs on hover

  var srcSrc;

  $('.js-results-image').hover(
    function() {
      if ($(this).attr('data-src') && $(this).attr('data-src').replace(/ /g,'') != "") {
        srcSrc = $(this).attr('src');
        $(this).attr('src', $(this).attr('data-src'));
      } else return;
    },
    function() {
      $(this).attr('data-src', $(this).attr('src'));
      $(this).attr('src', srcSrc);
    }
  );

  // end results image hover function

  // data close method - this can be reused
  $('body').on('click', '.close', function() {
    var parentId = $(this).data('ui-close');
    if (parentId === "#o-super-header__container") {
      document.cookie = "globalHeader=hidden;expires=Session;path=/";
    }
    $(parentId).hide();
  });

  $('body').on('click', '[data-remember="checkbox"]', function(){
    if(!$(this).is(':checked')) {
      document.cookie = "matalanUsername" + '=;expires=Thu, 01 Jan 1970 00:00:01 GMT;path=/';
    }
    else {
      var emailValue = $(this).closest('.o-form').find('[data-remember="email"]').val();
      document.cookie = "matalanUsername=" + emailValue +";path=/";
    }
  })




  // // pattern - make something active, remove active from siblings
  $('.o-filters__list__item .filter__colours .filter__colour').click(function(e) {
    $(this).toggleClass('active');
    $(this).siblings().removeClass('active');
    $(this).find("input[type=checkbox]").prop('checked', true);
    $(this).siblings().find("input[type=checkbox]").prop('checked', false);
  });


  // does .zoom exist?
  $(".zoom").bind("click", function(e) {
    var ez = $('.zoom').data('elevateZoom');
    $.fancybox(ez.getGalleryList());
    return false;
  });

  // Close select box on device orientation change
  $( window ).on( "orientationchange", function( e ) {
    e.stopPropagation()
    setTimeout(function () {
      if ($('select:focus').length > 0) {
        $('select:focus').blur();
      }
    }, 500);
  });


  // Filters Show more
  $('body').on('click', '.filters--show-more', function() {
    $(this).hide();
    $(this).parent().find('label').css('display', 'block');
  });


  // global alert event
  $(".global-test").click(function() {
    $(".c-alert-global").toggleClass("active");
  });


  // header - show mobile search input field
  $('body').on('click', '#header--mobile-search', function() {
    $(this).toggleClass('active');
    $('.header--search-bar').toggleClass('active').find('input').focus();
  });

  (function() {
    $('body').on('click', '.js-back-to-top', function() {
      $(window).scrollTop(0)
    });
  })();

  // search results layout switch
  (function() {

    $('body').on('click', '.js-results-tight', function(e) {
      e.preventDefault();
      if (!$(this).hasClass('active')) {
        document.cookie = "_matalan_product_view=tight;expires=Session;path=/";
        $(this).addClass('active')
          .siblings().removeClass('active')
          .closest('.search-results__main')
          .removeClass('search-results--wide')
          .addClass('search-results--tight');
      }
    });
    // search results layout switch cont.
    $('body').on('click', '.js-results-wide', function(e) {
      e.preventDefault();
      if (!$(this).hasClass('active')) {
        document.cookie = "_matalan_product_view=wide;expires=Session;path=/";

        $(this).addClass('active')
          .siblings().removeClass('active')
          .closest('.search-results__main')
          .removeClass('search-results--tight')
          .addClass('search-results--wide');
      }
    });

    $('body').on('click', '.js-results-more', function(e) {
        document.cookie = "_matalan_product_number=120;expires=Session;path=/";
    });

    $('body').on('click', '.js-results-less', function(e) {
        document.cookie = "_matalan_product_number=36;expires=Session;path=/";
    });
  })();







})(jQuery);



