() => {

  const noUiSlider = require("vendors/nouislider");
  delete localStorage.rangevalues;
  const sliderRangeStorage = localStorage.rangevalues ? JSON.parse(localStorage.rangevalues) : [];

  // =======================
  // filter display handling
  // =======================
  //
  var getUrlParameter = function getUrlParameter(sParam) {
    var s
    var sPageURL = decodeURIComponent(window.location.search.substring(1)),
      sURLVariables = sPageURL.split('&'),
      sParameterName,
      i;

    for (i = 0; i < sURLVariables.length; i++) {
      sParameterName = sURLVariables[i].split('=');

      if (sParameterName[0] === sParam) {
        return sParameterName[1] === undefined ? true : sParameterName[1];
      }
    }
  };

  const handleFilterFacets = () => {
    const filterCollapseItem = $(".o-filters__list-contain [data-toggle='collapse']");

    filterCollapseItem.each(function() {
      const filterTarget = $(this).attr('href');

      if (!window.isMobileOrTablet) {
        // show filters as open on desktop page load
        $(filterTarget).collapse('show');
      } else {
        // or close on mobile/tablet page load
        $(filterTarget).collapse('hide');
      }
    });
  };

  handleFilterFacets();

  // ===========================
  // filter display handling end
  // ===========================




  // ===========================
  // Range Slider init
  // ===========================

  const initRangeSlider = () => {

    const priceSliders = $('[data-rangeslider]');
    for (var i = 0; i < priceSliders.length; i++) {


      const sliderDisplay = [
        $(priceSliders[i]).find('.slider-display-lower'),
        $(priceSliders[i]).find('.slider-display-upper')
      ];

      const sliderValues = [
        $(priceSliders[i]).find('.slider-value-lower'),
        $(priceSliders[i]).find('.slider-value-upper')
      ];

      if (!sliderRangeStorage[i]) {
        sliderRangeStorage.push({ "min": sliderValues[0][0].dataset.rangevalue, "max": sliderValues[1][0].dataset.rangevalue })
      }

      if (window.location.search.includes('price') >= 1) {
        const minUrlValue = getUrlParameter(sliderValues[0][0].getAttribute('name'));
        const maxUrlValue = getUrlParameter(sliderValues[1][0].getAttribute('name'))
        if (typeof minUrlValue != "undefined") {
         $('[name="'+sliderValues[0][0].getAttribute('name') + '"]').val(minUrlValue);
        }
        if (typeof maxUrlValue != "undefined") {
         $('[name="'+sliderValues[1][0].getAttribute('name') + '"]').val(maxUrlValue);
        }
      }


      noUiSlider.create(priceSliders[i], {
        start: ((() => {
          if (window.location.search.includes('price') >= 1) {
            if (typeof getUrlParameter(sliderValues[0][0].getAttribute('name')) != "undefined") {
              return [
                parseInt(getUrlParameter(sliderValues[0][0].getAttribute('name'))),
                parseInt(getUrlParameter(sliderValues[1][0].getAttribute('name')))
              ];
            } else {
              return [
                parseInt(sliderValues[0][0].value) * 1,
                parseInt(sliderValues[1][0].value) * 1
              ];
            }
          } else {

            return [
              parseInt(sliderValues[0][0].value) * 1,
              parseInt(sliderValues[1][0].value) * 1
            ];
          }
        }))(),
        step: ((() => {
            if (sliderRangeStorage.length > 0) {
              var diff = (sliderRangeStorage[i].max * 1 - sliderRangeStorage[i].min * 1);
            } else {
              var diff = (sliderValues[1][0].dataset.rangevalue * 1 - sliderValues[0][0].dataset.rangevalue * 1);
            }

            if (diff <= 100) {
              return 0;
            } else if (diff > 100 && diff <= 500) {
              return 10;
            } else if (diff > 500 && diff <= 1000) {
              return 50;
            } else {
              return 100;
            }
        }))(),
        range: ((() => {
          if (sliderRangeStorage.length > 0) {
            return {
              "min": sliderRangeStorage[i].min * 1,
              "max": sliderRangeStorage[i].max * 1
            }
          } else {
            return {
              "min": sliderValues[0][0].dataset.rangevalue * 1,
              "max": sliderValues[1][0].dataset.rangevalue * 1
            }
          }
        }))(),
        connect: true
      });


      // value display - leave this one 'bounced' for speedy user feedback
      priceSliders[i].noUiSlider.on('update', (values, handle) => {

        sliderDisplay[handle][0].innerHTML = "&pound;" + Math.round(values[handle]).toFixed(0);
      });

      // debounced update of underlying inputs
      priceSliders[i].noUiSlider.on('change', _.debounce((values, handle) => {
        sliderValues[handle][0].value = Math.round(values[handle]).toFixed(1);
        $(sliderValues[handle][0]).trigger('change');
        $('.noUi-origin').attr('disabled', true);
      }, 0));

    }
    if (!localStorage.rangevalues) {
      localStorage.rangevalues = JSON.stringify(sliderRangeStorage)
    }

  };

  initRangeSlider();



  // ===========================
  // Range Slider End
  // ===========================



  // ===========================
  // Additional Handling
  // ===========================

  $('body').on('click', '.js-clear__filters', () => {

    // clear any selected radios / checkboxes in the filter sidebar
    $('.o-filters__inner').find('input[type=checkbox]:checked').removeAttr('checked');
    $('.o-filters__inner').find('input[type=radio]:checked').removeAttr('checked');

    // disable the range slider before submit
    $('[data-rangeslider] input').each(function(i) {
      $(this).prop('disabled', true);
    });

    //apply filters
    $('.filter__header__apply').trigger('click');
    //close modal
    $('.o-filters__contain').modal('hide');
  });


  // close modal on click of filters apply
  $('body').on('click', '.filter__header__apply', e => {
    $('.o-filters__contain').modal('hide');
  });


  $(document).on('pjax:complete', e => {
    initRangeSlider();
    handleFilterFacets();
  });


  const filterContain = $('#filters__contain');
  filterContain.on('hidden.bs.modal', function() {
    $(this).attr('style', '');
  });

  // ===========================
  // Additional Handling End
  // ===========================



  // ===========================
  // Helpers
  // ===========================

  // ====================================================================
  // pluck values from the URL arguments
  //
  var getQueryVariable = variable => {
      const query = decodeURIComponent(window.location.search.substring(1));
      const vars = query.split("&");

      for (let i = 0; i < vars.length; i++) {
        const pair = vars[i].split("=");
        if (pair[0] == variable) {
          return pair[1];
        }
      }
      return (false);
    }
    // =====================================================================
}();



